import React, { useContext, useEffect, useState } from "react";

import { Button, message, Typography } from "antd";
import { useNavigate } from "react-router-dom";

import { CardsArea } from "../../components/cards_area";
import { CloseBtn } from "../../components/close_button/close_button";
import { Loader } from "../../components/loader";
import { BasicModal } from "../../components/modal/basic_modal";
import { PlayedNumbersHeader } from "../../components/played_numbers_header/played_numbers_header";
import { Question } from "../../components/questions_block/question";
import { PULL } from "../../constants";
import {
  Box,
  TableContetnContainer,
  GamingTable,
  Coin,
  FlexCenteredContainer,
} from "../../global_styles";
import { CurrentGameContext } from "../../providers/current_game";
import q from "../../questions/questions.json";
import { generateMultiplayer } from "../../utils";

export const MainPage = () => {
  const { currentGame, updateCurrentGame } = useContext(CurrentGameContext);
  const navigate = useNavigate();

  const [actualNumbers, setActualNumbers] = useState([...PULL]);
  const [usedNumbers, setUsedNumbers] = useState([]);
  const [actualNumber, setActualNumber] = useState();

  const [users, setUsers] = useState([]);
  const [showCoin, setShowCoin] = useState(false);
  const [loading, setLoading] = useState(true);

  const questionsPull = q.questions;
  const [currentQuestion, setCurrentQuestion] = useState(null);

  const [isOpenModal, setIsOpenModal] = useState(false);
  const [isOpenWinnerModal, setIsOpenWinnerModal] = useState(false);
  const [loseModal, setLoseModal] = useState(false);

  const [isShow, setIsShow] = useState(false);
  const isShowCoin = showCoin && actualNumber;
  const isSingle = currentGame?.wrongAnswers >= 3;

  const isIncludesError = users[0]?.card?.some((e) => e === "error");
  const loseText = isIncludesError
    ? `Гру завершено. Ви припустились ${currentGame?.wrongAnswers} помил${
        currentGame?.wrongAnswers > 1 ? "ок" : "ки"
      }`
    : "На жаль, ви припустились 3 помилок. Гру завершено";

  const startNewGame = () => {
    const gameSettings = JSON.parse(localStorage.getItem("game"));

    if (!!gameSettings) {
      updateCurrentGame(gameSettings);
      setUsers(gameSettings.players);
      setTimeout(() => {
        setLoading(false);
        setIsOpenModal(true);
      }, 1500);
    } else {
      message.error(
        "Виникла помилка. Поверніться до стартової сторінки та зареєструйте гравців"
      );
      setTimeout(() => {
        navigate("/");
      }, 2500);
    }
  };

  const getRandomNumber = () => {
    return Math.floor(Math.random() * actualNumbers.length);
  };

  const randomNumber = () => {
    let currentArray = actualNumbers;
    if (currentArray.length) {
      setShowCoin(true);
      const currNumber = currentArray[getRandomNumber()];
      setUsedNumbers((prev) => [currNumber, ...prev]);
      setActualNumber(currNumber);
      const updArray = currentArray.filter((n) => n !== currNumber);
      setActualNumbers(updArray);
      setTimeout(() => {
        checkNumber(currNumber);
      }, 4000);
    } else {
      message.warning("Всі ігрові номери використані");
    }
  };

  const checkNumber = (number) => {
    const usersWithNumbers = users?.filter((user) =>
      user?.card.includes(number)
    );
    if (usersWithNumbers?.length) {
      handleAnswer(usersWithNumbers, number);
    } else {
      setShowCoin(false);
      showNewNumber();
    }
  };

  const handleAnswer = (users, number) => {
    const findQuestion = questionsPull.filter((q) => q.id === `qb${number}`);
    setCurrentQuestion({ users, q: findQuestion[0], number });
  };

  const checkWinner = () => {
    let isWinner = false;
    users?.forEach((user) => {
      if (user?.card?.every((e) => typeof e === "string")) {
        setIsOpenWinnerModal(true);
        setIsShow(false);
        isWinner = true;
      }
    });
    if (!isWinner) {
      setTimeout(() => {
        randomNumber();
      }, 150);
    } else {
      return;
    }
  };

  const checkLose = () => {
    if (currentGame.type === "single" && currentGame?.wrongAnswers >= 3) {
      setLoseModal(true);
      setIsShow(false);
      setUsedNumbers([]);
      setActualNumber(null);
      setCurrentQuestion(null);
      return;
    }
  };

  const getWinnerName = () => {
    const idx = users.findIndex((user) =>
      user?.card?.every((e) => typeof e === "string")
    );
    return idx >= 0 ? users[idx].name : "empty";
  };

  const showNewNumber = () => {
    setShowCoin(false);
    if (currentGame.type === "single") {
      checkLose();
    }
    checkWinner();
  };

  const startGame = () => {
    setIsShow(true);
    showNewNumber();
    setIsOpenModal(false);
    setCurrentQuestion(null);
    setUsedNumbers([]);
    setActualNumbers([...PULL]);
    setActualNumber(null);
    setCurrentQuestion(null);
  };

  const restart = () => {
    setUsedNumbers([]);
    setActualNumbers([...PULL]);
    setActualNumber(null);
    setCurrentQuestion(null);
    const names = users.map((player) => player.name);
    const multiplayer = generateMultiplayer(names);
    currentGame.players = [...multiplayer];
    if (currentGame.type === "single") {
      currentGame.wrongAnswers = 0;
    }
    updateCurrentGame(currentGame);
    localStorage.setItem("game", JSON.stringify(currentGame));
    setLoseModal(false);
    setIsOpenWinnerModal(false);
    setTimeout(() => {
      startNewGame();
    }, 1000);
  };

  const finish = () => {
    localStorage.clear();
    navigate("/");
  };

  const findCategory = () => {
    const catName = questionsPull.filter((q) => q.id === `qb${actualNumber}`);
    return catName.length ? catName[0].category : "default";
  };

  const actionButtons = (
    <Box p="20px 0" style={{ display: "flex" }}>
      <FlexCenteredContainer>
        <Box m="0 5px">
          <Button
            onClick={finish}
            size="large"
            danger
            type="primary"
            shape="round"
          >
            {isSingle ? "ВИЙТИ" : "ЗАВЕРШИТИ ГРУ"}
          </Button>
        </Box>
        <Box>
          <Button onClick={restart} ghost shape="round" size="large">
            ПОВТОРИТИ
          </Button>
        </Box>
      </FlexCenteredContainer>
    </Box>
  );

  useEffect(() => {
    startNewGame();
  }, []);

  return (
    <>
      {loading && <Loader />}
      {!loading && (
        <div>
          <CloseBtn />
          <>
            <PlayedNumbersHeader usedNumbers={usedNumbers} />
            <GamingTable>
              {isShow && (
                <TableContetnContainer
                  minWidth="350px"
                  minHeight="350px"
                  bgc="#ffeddd"
                >
                  {!!currentQuestion && (
                    <Question
                      question={currentQuestion}
                      setCurrentQuestion={setCurrentQuestion}
                      showNewNumber={showNewNumber}
                    />
                  )}
                </TableContetnContainer>
              )}
              {isShow && (
                <TableContetnContainer minWidth="250px" bgc="transparent">
                  <FlexCenteredContainer>
                    {isShowCoin && (
                      <Box>
                        <Typography.Title
                          style={{ textAlign: "center" }}
                          level={4}
                          italic
                        >
                          Категорія {findCategory()}
                        </Typography.Title>
                        <Coin>{actualNumber}</Coin>
                      </Box>
                    )}
                  </FlexCenteredContainer>
                </TableContetnContainer>
              )}
            </GamingTable>
          </>
          {isShow && <CardsArea players={users} />}
        </div>
      )}
      <BasicModal isModalOpen={isOpenModal} title="Початок гри">
        <Box style={{ height: 150 }}>
          <FlexCenteredContainer>
            <Button onClick={startGame} size="large" shape="round" block ghost>
              СТАРТ
            </Button>
          </FlexCenteredContainer>
        </Box>
      </BasicModal>

      <BasicModal
        isModalOpen={isOpenWinnerModal}
        title={isSingle ? "" : "Вітання"}
      >
        <Box style={{ height: 150, textAlign: "center" }}>
          <Typography.Title level={2}>
            {currentGame.type === "single"
              ? `${getWinnerName()}, Вітаємо з перемогою!!!`
              : `Переможець ${getWinnerName()}`}
          </Typography.Title>
          {actionButtons}
        </Box>
      </BasicModal>

      <BasicModal isModalOpen={loseModal}>
        <Box style={{ height: 150, textAlign: "center" }}>
          <Typography.Title level={3}>{loseText}</Typography.Title>
          {actionButtons}
        </Box>
      </BasicModal>
    </>
  );
};
