import React, { useContext, useState } from "react";

import { Button, Input } from "antd";
import { useNavigate } from "react-router-dom";

import {
  ActionsContainer,
  Box,
  ContentContainer,
} from "../../../global_styles/global_styles";
import { CurrentGameContext } from "../../../providers";
import { createUser } from "../../../utils";

export const SinglePlayerTab = () => {
  const { currentGame, updateCurrentGame } = useContext(CurrentGameContext);
  const navigate = useNavigate();
  const [player, setPlayer] = useState();
  const inputHandler = (e) => {
    setPlayer((prev) => ({ ...prev, [e.target.name]: e.target.value }));
  };

  const startSingleGame = () => {
    const singlePlayer = createUser(player.name);
    currentGame.players = [singlePlayer];
    currentGame.wrongAnswers = 0;
    updateCurrentGame(currentGame);
    localStorage.setItem("game", JSON.stringify(currentGame));
    navigate("/game");
  };
  return (
    <Box>
      <ContentContainer height="256px">
        <Input
          name="name"
          shape="round"
          placeholder="імʼя користувача"
          value={player?.name || ""}
          onChange={inputHandler}
        />
      </ContentContainer>
      <ActionsContainer>
        <Button
          style={{ width: 250 }}
          shape="round"
          ghost
          disabled={!player?.name}
          onClick={startSingleGame}
        >
          РОЗПОЧАТИ ГРУ
        </Button>
      </ActionsContainer>
    </Box>
  );
};
