import React, { useEffect, useMemo, useState } from "react";

import { Avatar } from "antd";
import PropTypes from "prop-types";

import { Header } from "./style";
import { DEBOUNCED_TIME } from "../../constants";
import { Box } from "../../global_styles";

export const PlayedNumbersHeader = ({ usedNumbers }) => {
  const [numbers, setNumbers] = useState([]);

  useEffect(() => {
    setTimeout(() => {
      setNumbers(usedNumbers);
    }, DEBOUNCED_TIME);
  }, [usedNumbers]);

  return (
    <Header>
      <Box style={{ textAlign: "center" }}>
        <span>Зігравші номери: </span>
      </Box>
      <Box
        m="0 auto"
        style={{
          display: "flex",
          flexWrap: "wrap",
          height: "42px",
          overflow: "auto",
        }}
      >
        {numbers.map((number, i) => (
          <Box
            key={i}
            m="5px"
            style={{ transition: "all 1s ease-out", fontFamily: "MakBold" }}
          >
            <Avatar
              style={{
                backgroundColor: "#ec9da1",
                color: "black",
                verticalAlign: "middle",
              }}
              size="medium"
            >
              {number}
            </Avatar>
          </Box>
        ))}
      </Box>
    </Header>
  );
};

PlayedNumbersHeader.propTypes = {
  usedNumbers: PropTypes.array,
};
