import React, { useContext } from "react";

import { Tabs } from "antd";

import { Wrapper, TabsArea } from "./style";
import { CurrentGameContext } from "../../providers";
import { SinglePlayerTab, MultiplayerTab } from "../start_page_tabs";

export const GameTypeSwitcher = () => {
  const { currentGame, updateCurrentGame } = useContext(CurrentGameContext);
  const onChange = (key) => {
    currentGame.type = key;
    updateCurrentGame(currentGame);
  };
  return (
    <Wrapper>
      <TabsArea>
        <Tabs defaultActiveKey={currentGame.type} onChange={onChange}>
          <Tabs.TabPane tab="Одиночна гра" key="single">
            <SinglePlayerTab />
          </Tabs.TabPane>
          <Tabs.TabPane tab="Мультиплеєр" key="multiplayer">
            <MultiplayerTab />
          </Tabs.TabPane>
        </Tabs>
      </TabsArea>
    </Wrapper>
  );
};
