import React, { useState, useContext, useEffect } from "react";

import { CloseCircleOutlined } from "@ant-design/icons";
import { Button, Input, message } from "antd";
import { useNavigate } from "react-router-dom";

import {
  ActionsContainer,
  Box,
  ContentContainer,
} from "../../../global_styles/global_styles";
import { CurrentGameContext } from "../../../providers/current_game";
import { createUser, generateMultiplayer } from "../../../utils";

export const MultiplayerTab = () => {
  const { currentGame, updateCurrentGame } = useContext(CurrentGameContext);
  const navigate = useNavigate();
  const [players, setPlayers] = useState([createUser(""), createUser("")]);

  const inputHandler = (e) => {
    const updPlayers = players.map((player) => {
      if (player.id === +e.target.name) {
        player.name = e.target.value;
        return player;
      } else {
        return player;
      }
    });
    setPlayers(updPlayers);
  };

  const addPlayer = () => {
    if (players.length < 12) {
      setPlayers((prev) => [...prev, createUser("")]);
    }
  };

  const checkPlayersLength = () => {
    return !!(players?.length >= 12);
  };

  const removePlayer = (id) => {
    const updPlayers = players.filter((player) => player.id !== id);
    setPlayers(updPlayers);
    if (updPlayers.length <= 2) {
      message.error("Для мультиплеєра мінімальна кількість гравців 2");
    }
  };

  const checkNames = () => {
    return !!(
      players.every((player) => player?.name.trim() !== "") &&
      players.length >= 2
    );
  };

  const startMultiplayerGame = () => {
    const names = players.map((player) => player.name);
    const multiplayer = generateMultiplayer(names);
    currentGame.players = [...multiplayer];
    updateCurrentGame(currentGame);
    localStorage.setItem("game", JSON.stringify(currentGame));
    navigate("/game");
  };
  useEffect(() => {
    if (players.length === 12) {
      message.error("Максимальна кількість гравців 12");
    }
  }, [players]);

  return (
    <Box>
      <ContentContainer height="204px">
        {players.map((player, i) => (
          <Box key={player.id} m="5px auto" style={{ display: "flex" }}>
            <Input
              name={player.id}
              value={player.name || ""}
              placeholder={` імʼя користувача ${i + 1}`}
              onChange={inputHandler}
            />

            <Box m="0 0 0 5px">
              <Button
                type="primary"
                icon={<CloseCircleOutlined />}
                onClick={() => removePlayer(player.id)}
                danger
              />
            </Box>
          </Box>
        ))}
      </ContentContainer>
      <ActionsContainer style={{ flexDirection: "column" }}>
        <Box m="0 auto" width="100%">
          <Button
            shape="round"
            onClick={addPlayer}
            disabled={checkPlayersLength()}
            style={{ width: 250 }}
            ghost
          >
            ДОДАТИ ГРАВЦЯ
          </Button>
        </Box>
        <Box m="10px auto" width="100%">
          <Button
            shape="round"
            onClick={startMultiplayerGame}
            disabled={!checkNames()}
            style={{ width: 250 }}
            ghost
          >
            РОЗПОЧАТИ ГРУ
          </Button>
        </Box>
      </ActionsContainer>
    </Box>
  );
};
