import React from "react";

import { CheckOutlined, CloseOutlined } from "@ant-design/icons";
import { Avatar, Card, Space } from "antd";
import PropTypes from "prop-types";

import { Box } from "../../global_styles";

export const PlayerCard = ({ player }) => (
  <Space direction="vertical" size={16}>
    <Card
      size="small"
      title={player.name}
      style={{
        width: 180,
        justifyContent: "center",
      }}
    >
      <Box m="5px auto" style={{ fontFamily: "MakBold" }}>
        {player.card.map((n, i) => {
          if (n === "win") {
            return (
              <Avatar
                style={{ margin: 3, background: "#3EB489" }}
                key={i}
                size="medium"
              >
                <CheckOutlined />
              </Avatar>
            );
          }
          if (n === "error") {
            return (
              <Avatar
                style={{ margin: 3, background: "tomato" }}
                key={i}
                size="medium"
              >
                <CloseOutlined />
              </Avatar>
            );
          } else {
            return (
              <Avatar
                style={{ margin: 3, background: "#efd7be", color: "black" }}
                key={i}
                size="medium"
              >
                {n}
              </Avatar>
            );
          }
        })}
      </Box>
    </Card>
  </Space>
);

PlayerCard.propTypes = {
  player: PropTypes.object,
};
