import React, { useState, useContext } from "react";

import { Button, Typography, Radio, Space } from "antd";
import PropTypes from "prop-types";

import { Box } from "../../global_styles/global_styles";
import { CurrentGameContext } from "../../providers";

export const Question = ({ question, setCurrentQuestion, showNewNumber }) => {
  const { q, users, number } = question;
  const randomItem = () => Math.floor(Math.random() * q?.questions?.length);

  const { currentGame, updateCurrentGame } = useContext(CurrentGameContext);
  const [answer, setAnswer] = useState();
  const [players, setPlayers] = useState(users);
  const [randomQuestion, setRandomQuestion] = useState(
    q?.questions[randomItem()]
  );

  const onChange = (e) => {
    setAnswer(e.target.value);
  };

  const removePlayerAfterAnswer = (player) => {
    const updPlayers = players.filter((pl) => pl.id != player.id);
    if (updPlayers.length > 0) {
      setRandomQuestion(q?.questions[randomItem()]);
    } else {
      setCurrentQuestion(null);
      showNewNumber();
    }
    setPlayers(updPlayers);
  };

  const checkAnswer = () => {
    const index = players[0].card.findIndex((i) => i === number);
    if (answer === randomQuestion.correctAnswer) {
      players[0].card.splice(index, 1, `win`);
    } else {
      players[0].card.splice(index, 1, `error`);
      if (currentGame.type === "single") {
        currentGame.wrongAnswers = currentGame.wrongAnswers + 1;
      }
    }
    const idx = currentGame.players.findIndex((p) => p.id === players[0].id);

    currentGame.players[idx] = players[0];
    updateCurrentGame(currentGame);
    setRandomQuestion(null);
    removePlayerAfterAnswer(players[0]);
    setAnswer(null);
  };
  return (
    <Box p="1rem">
      <Typography.Title
        level={2}
        style={{ textAlign: "center", fontFamily: "Medium" }}
      >
        Запитанння для гравця {players.length && players[0].name}
      </Typography.Title>

      <Box style={{ flexDirection: "column", height: "100%" }}>
        <Typography.Title level={4}>{randomQuestion?.text}</Typography.Title>
        <Box m="5px 10px" style={{ fontFamily: "Regular" }}>
          <Radio.Group onChange={onChange} value={answer}>
            <Space direction="vertical">
              {randomQuestion?.answers.map((answer, i) => {
                return (
                  <Radio key={i} value={answer} style={{ margin: "5px 0" }}>
                    {answer}
                  </Radio>
                );
              })}
            </Space>
          </Radio.Group>
        </Box>
        <Box
          style={{ maxWidth: 600, display: "flex", justifyContent: "center" }}
          m="20px auto 5px"
        >
          <Button
            onClick={checkAnswer}
            disabled={!answer}
            style={{ width: 250 }}
            ghost
            shape="round"
          >
            ВІДПОВІСТИ
          </Button>
        </Box>
      </Box>
    </Box>
  );
};

Question.propTypes = {
  question: PropTypes.object,
  setCurrentQuestion: PropTypes.func.isRequired,
  showNewNumber: PropTypes.func.isRequired,
};
